.container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 30px 10px 0 10px;
}
.main{
    display: flex;
    flex-direction: column;
    width: 100%;
}
.name{
    position: relative;
    color: #F5E0A3;
    font-size: 36px;
    text-align: center;
    padding-bottom: 20px;
    word-wrap: break-word;
}
.name h1{
    cursor: pointer;
}
.name h1::selection{
    background-color: transparent;
}
.perexod{
    color:  #E8222D;
    font-size: 24px;
}
.perexod h4{

}
.perexod a{
    color: #b3000c;
    text-decoration: none;
    margin-right: 15px;
    transition: all 0.3s ease;
}
.perexod span{
    color: #b3000c;
    cursor: pointer;
    transition: all 0.3s ease;
}
.perexod span:hover{
    color: #66000a;
}
.perexod a:hover{
    color: #66000a;
}
.back{
    position: absolute;
    right: 10px;
    top: -25px;
    color: #E8222D;
    cursor: pointer;
    text-decoration: none;
    transition: all 0.3s ease;
}
.back:hover{
    color: #b3000c;
}
.row_1{
    display: flex;
    flex-direction: row;
}
.row_1_container_img{
    max-width: 600px;
}
.row_1 img{
    max-width: 100%;
    height: auto;
    background-color: #080115;
}
.time{
    color: #E8222D;
    padding-bottom: 10px;
}
.time b{
    font-size: 30px;
}
.row_1_discript{
    padding: 20px;
    font-size: 24px;
    color: #f8c66b;
}
.row_2_ing{
    margin-top: 10px;
    background-color: #f8c66b;
    border-radius: 15px;
    margin-bottom: 15px;
    color: #200A3E;
}
.row_2_zag{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    font-size: 28px;

    padding: 10px;
}
.ingridients_container{
    display: flex;
    flex-direction: column;
    font-size: 24px;
}
.ingredientsElem{
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    padding: 0 20px;
    align-items: flex-end;
}
.ingredientsElem:last-child{
    margin-bottom: 15px;
}
.ingridients_container>div:nth-child(2n - 1){
    background-color:  #F5E0A3;
}
.yslovna9Ed{
    display: flex;
    align-items: center;
}
.yslovna9Ed input{
    font-size: 24px;
    width: 100px;
    border: 1px solid #200A3E;
    margin: 0 5px;
    outline: none;
}
.row_3_xod{
    margin-bottom: 20px;
}
.row_3_xod h1{
    color: #E8222D;
    text-align: center;
    font-size: 50px;
    margin-bottom: 10px;
}
.xod_container{
    display: flex;
    flex-direction: column;
}
.xod_element{
    padding: 10px;
    border-radius: 10px;
    border: 2px solid #F5E0A3;
    color: #f8c66b;
    margin-bottom: 10px;
}
.one_str{
    font-size: 24px;
}
.two_str{
    font-size: 20px;
}


@media (max-width: 1000px) {
    .row_1{
        flex-direction: column;
    }
    .row_1_container_img{
       margin:  0 auto;
    }
}
@media (max-width: 400px) {
    .row_3_xod h1 {
        font-size: 30px;
    }
    .row_2_zag h2{
        font-size: 30px;
    }
}