.container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px;
}
.main{

}
.list{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.heading{

}
.search{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.search input{
    width: 90%;
    outline: none;
    border: 2px solid #E8222D;
    font-size: 24px;
    padding: 10px;
    background-color: transparent;
    border-radius: 30px;
    color: #F5E0A3;
    z-index: 5;
}
.lypa{
    position: absolute;
    height: 30px;
    filter: invert(1);
    z-index: 1;

}
.okoloFillter{
    padding: 10px 0;
}
.fillter{
    display: flex;
    width: 100%;
    justify-content: space-between;
    color: #F5E0A3;
    font-size: 32px;
    flex-wrap: wrap;
}
.button{
    padding-left: 30px;
    cursor: pointer;
    transition: all 0.3s ease;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    padding-right: 10px;
}
.button:hover{
    color: #f8c66b;
}
.back{
    text-decoration: none;
    padding-right: 50px;color: #F5E0A3;
    cursor: pointer;
    transition: all 0.3s ease;
}
.back:hover{
    color: #f8c66b;

}

.showFillter{
    color: #200A3E;
    font-size: 24px;
    background-color: #F5E0A3;
    border-bottom-right-radius: 15px;
    border-top-right-radius: 15px;
    border-bottom-left-radius: 15px;
    margin: 0 0 10px 0;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.showFillter div{
    padding: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.showFillter div::after{
  /* content: "\2191;";
    content: "\2193;";
    content: "\25B2;";
    content: "\25BC;";*/
}
.showFillter div:hover{
    color: #E8222D;

}
/*********************************/
.elemMain{
    width: 280px;
    height: 400px;
    display: block;
    padding: 10px;
    color: #F5E0A3;
    text-decoration: none;
    position: relative;
    margin-bottom: 10px;
}
.elemMain:hover .elemVspl{
    opacity: 1;
}
.elemMain:hover{
    transform: translateY(-3px);
}
.elemImg{
    width: 280px;
    height: 320px;
    /*clip: rect(40px, 40px, 40px, 40px);*/
    object-fit: cover;
    background-color: #00092d;
}

.elemConText{
    background-color: #E8222D;
    margin-top: -5px;
    padding: 5px!important;
    height: 90px;
    overflow: hidden;
}
.elemVspl{
    position: absolute;
    top: 10px;
    background-color: #E8222D;
    width: 260px;
    padding: 10px;
    transition: all 0.3s ease;
    opacity: 0;
}
.elemZag{
    font-size: 24px;
    word-break: break-all;
    margin-bottom: 8px;
    max-height: 70px;
    overflow: hidden;
}
.elemIng{

    color: #f8c66b;
}
.elemIng:hover::after {
    position: absolute;
    left: 150px;
    top: 360px;
    padding: 10px;
    background-color: #F5E0A3;
    color: #200A3E;
    content: attr(data-title);
}
.no_find{
    width: 100%;
    color: #f8c66b;
    text-align: center;
    font-size: 36px;
}


.preloader_container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.preloader_style{
    width: 200px;
}


@media screen and (max-width: 650px) {
    .list{
        justify-content: center;
    }
    .fillter{
        flex-direction: column-reverse;
    }
    .showFillter{
        border-top-right-radius: 0px;
    }
}







