.container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px 10px 0 10px;
}
.main{
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
    color: #f8c66b;
}

.back{
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: 30px;
    color: #E8222D;
    text-decoration: none;
    cursor: pointer;
    transition: all 0.3s ease;
}
.back:hover{
    color: #ffa5a7;

}
.delete{
    position: absolute;
    right: 10px;
    top: 45px;
    font-size: 30px;
    color: #E8222D;
    text-decoration: none;
    cursor: pointer;
    transition: all 0.3s ease;
}
.delete:hover{
    color: #ffa5a7;

}

.ingridient_add{
    color: #f8c66b;
    font-size: 24px;
    display: flex;
    align-items: center;
    border: 1px solid #f8c66b;
    margin-right: 8px;
    border-radius: 10px;
    padding: 5px;
    margin-bottom: 5px;
}
.ingridient_add_delete{
    border: 1px solid #E8222D;
    color: #E8222D!important;
    font-size: 20px!important;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin-left: 5px;
    margin-right: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.ingridient_add_delete:hover{
    background-color: #f8c66b;
}
.progress_add{
    display: flex;
    flex-direction: column;
    color: #f8c66b;
    padding: 5px;
    border: 1px solid #f8c66b;
    position: relative;
    border-radius: 15px;
    margin-bottom: 8px;
}
.progress_add h3{
    font-size: 24px;
    word-wrap: break-word;
}
.progress_add p{
    font-size: 20px;
    word-wrap: break-word;
}
.progress_add_delete{
    position: absolute;
    right: 5px;
    top: 5px;
    border: 1px solid #E8222D;
    color: #E8222D!important;
    font-size: 20px!important;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s ease;
}
.progress_add_delete:hover{
    background-color: #f8c66b;
}

.information{
    font-size: 24px;
    margin: 20px 0;
    width: 60%;
}
.poster_container{
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}
.poster_container span{
    font-size: 28px;
}
.poster{
    width: 50%;
}
.poster_hidden_input{
    position: relative;
}
.poster_file_input{
    position: absolute;
    bottom: 0;
    width: 50%;
    height: 50px;
    opacity: 0;
    z-index: 2;
    cursor: pointer;
}
.poster_file_button{
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 28px;
    width: 50%;
    height: 50px;
    background-color: rgba(32, 10, 62, 0.69);
    z-index: 1;
    opacity: 0;
    transition: all 0.3s ease;
}
.poster_hidden_input:hover .poster_file_button{
    opacity: 1;
}
.name_container{
    font-size: 28px;
    display: flex;
    flex-direction: column;
    margin-bottom: 5px;
}
.name{
    font-size: 32px;
    cursor: default;
}

.description_container{
    font-size: 28px;
    margin-bottom: 5px;
    display: flex;
    flex-direction: column;
}
.description{
    cursor: default;
}
.time_container{
    display: flex;
    font-size: 28px;
    margin-bottom: 5px;
    flex-wrap: wrap;
}
.time_container span{
    margin-right: 10px;
}
.time{
    cursor: default;
}
.time_container input{
    width: 75px;
}
.hard_container{
    display: flex;
    font-size: 28px;
    margin-bottom: 5px;
}
.hard{
    cursor: default;
}
.hard_container span{
    margin-right: 10px;
}
.hard_container input{
    width: 75px;
}
.cost_container{
    display: flex;
    font-size: 28px;
    margin-bottom: 5px;
    flex-wrap: wrap;
}
.cost_container input{
    width: 75px;
}
.cost{
    cursor: default;
}
.cost_container span{
    margin-right: 10px;
}
.ingredients_container{
    font-size: 28px;
    margin-bottom: 5px;
}
.ingredients_container>span{
    margin-bottom: 5px;
}
.ingredients{
    display: flex;
    flex-wrap: wrap;
}
.ingredients_add{
    transition: all 0.3s ease;
}

/*progress------------*/
