.progress_container_input{
    display: flex;
    flex-direction: column;
}
.progress_container_input_name{
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    flex-wrap: wrap;
 }
.progress_input_textarea{
    outline: none;
    font-size: 24px;
    color: #f8c66b;
    background-color: transparent;
    border: 1px solid #E8222D;
    border-radius: 5px;
    resize: none;
}
.progress_container_input_buttoncroup{
    display: flex;
    align-items: center;
    order: 2;
}
.progress_input_text{
    outline: none;
    font-size: 24px;
    color: #f8c66b;
    background-color: transparent;
    border: 1px solid #E8222D;
    border-radius: 5px;
    order: 1;
}
/*.progress_add_plus:hover{
    background-color: #E8222D;
}*/
.progress_input_add{
    font-size: 20px;
    border: 1px solid  #E8222D;
    padding: 5px 10px;
    border-radius: 10px;
    margin-right: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.progress_input_add:hover{
    background-color: #E8222D;
}
.progress_input_minus{
    border: 1px solid  #E8222D;
    padding: 0 10px;
    border-radius: 10px;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 0.3s ease;
}
.progress_input_minus:hover{
    background-color: #E8222D;
}
.progress_add_plusss{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px;
    padding: 5px 0;
    border-radius: 10px;
    border: 1px solid #f8c66b;
    cursor: pointer;
    transition: all 0.3s ease;
}
.progress_add_plusss:hover{
     background-color: #E8222D;
 }
/*------------------*/

.input_text{
    outline: none;
    font-size: 24px;
    color: #f8c66b;
    background-color: transparent;
    border: 1px solid #E8222D;
    border-radius: 5px;
}
.textarea_text{
    outline: none;
    font-size: 20px;
    color: #f8c66b;
    background-color: transparent;
    border: 1px solid #E8222D;
    border-radius: 5px;
    resize: none;
}

.ingredients_input_container{
    display: flex;
    flex-wrap: wrap;

}
.ingredients_input_text{
    outline: none;
    font-size: 20px;
    color: #f8c66b;
    background-color: transparent;
    border: none;

}
.ingredients_input_number{
    outline: none;
    font-size: 20px;
    color: #f8c66b;
    background-color: transparent;
    border-left: 1px solid #f8c66b;
    border-right: 1px solid #f8c66b;
    border-top: none;
    border-bottom: none;

    width: 75px;

}
.ingredients_input_add{
    padding: 0 12px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.ingredients_input_add:hover{
    color: #E8222D;
}
.ingredients_input_minus{
    padding: 0 12px;
    cursor: pointer;
    transition: all 0.3s ease;
    border-left: 1px solid #f8c66b;
}
.ingredients_input_minus:hover{
    color: #E8222D;
}
.ingredients_input_add_main{
    padding: 0 45px;
}
.save{
    width: 100%;
    padding: 8px 0;
    border-radius: 10px;
    cursor: pointer;
    background-color: #f8c66b;
    transition: all 0.3s ease;
    border: 1px solid #f8c66b;
    font-size: 24px;
    display: flex;
    justify-content: center;
    color: #200A3E;
    margin-bottom: 30px;
}
.save:hover{
    color: #E8222D;
    border-color: #E8222D;
}
@media screen and (max-width: 700px) {
    .poster{
        width: 100%;
    }
    .poster_file_input{
        width: 100%;
    }
    .poster_file_button{
        width: 100%;
    }
}
@media screen and (max-width: 450px) {
    .ingredients_input_text{
        width: 120px;
    }

}
@media screen and (max-width: 490px) {
    .progress_container_input_buttoncroup{
        order: 1;
        width: 100%;
        margin-bottom: 5px;
        justify-content: flex-end;
    }
    .progress_input_text{
        order: 2;
        width: 100%;
    }

}








