.container{
    max-width: 1200px;
    margin: 0 auto;

    padding: 20px 10px 0 10px;
}
.main{

}
.zagMain{
    color: #f8c66b;
    font-size: 42px;
    text-align: center;
    margin-bottom: 15px;
}
.formAdd{
    display: flex;
    flex-direction: column;
}
.formAdd span{
    color: #f8c66b;
    font-size: 32px;
}
.formAdd input{
    font-size: 24px;
    color: #f8c66b;
    background-color: transparent;
    border: 1px solid #E8222D;
    border-radius: 5px;
    outline: none;
}
.formAdd_textArea{
    font-size: 20px;
    color: #f8c66b;
    background-color: transparent;
    border: 1px solid #E8222D;
    border-radius: 5px;
    outline: none;
    resize: none;
}
.formAdd_con{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.formAdd_con span{
    padding-right: 10px;
}
.formAdd_button{
    color: #f8c66b;
    background-color: transparent;
    border: 1px solid #E8222D;
    outline: none;
    margin-top: 10px;
    padding: 5px;
    font-size: 30px;
    border-radius: 15px;
    margin-bottom: 30px;
}
.formAdd_button:hover{
    background-color: #E8222D;
    cursor: pointer;
}
.form_ingridient_add_1{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.form_ingridient_add_1 p{
    color: #f8c66b;
    font-size: 30px;
    padding: 0 5px;
}
.form_ingridient_add_2{
    display: flex;
    align-items: center;
}
.form_ingridient_add_2 p{
    color: #f8c66b;
    font-size: 30px;
    padding: 0 5px;
}
.form_ingridient_add{
    display: flex;
    flex-wrap: wrap;
}
.form_ingridient_add_button{
    font-size: 36px;
    border: 1px solid #E8222D;
    color: #E8222D;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin-left: 20px;
    transition: all 0.3s ease;
    cursor: pointer;
}
.form_ingridient_add_button:hover{
    background-color: #f8c66b;
}
.form_ingridients{
    margin-top: 5px;
}
.form_ingridient_add_element{
    color: #f8c66b;
    font-size: 24px;
    display: flex;
    align-items: center;
    border: 1px solid #f8c66b;
    margin-right: 8px;
    border-radius: 10px;
    padding: 5px;
}
.form_ingridient_add_element_delete{
    border: 1px solid #E8222D;
    color: #E8222D!important;
    font-size: 20px!important;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin-left: 5px;
    margin-right: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.form_ingridient_add_element_delete:hover{
    background-color: #f8c66b;
}
.form_ingridient_obj{
    display: flex;
    flex-wrap: wrap;
}
.form_progress_add{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}
.form_progress_add_1{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.form_progress_add_1 p{
    color: #f8c66b;
    font-size: 30px;
    padding: 0 5px;
}
.form_progress_add_2{
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
}
.form_progress_add_2 p{
    color: #f8c66b;
    font-size: 30px;
    padding: 0 5px;
}

.form_progress_add_element{
    display: flex;
    flex-direction: column;
    color: #f8c66b;
    padding: 5px;
    border: 1px solid #f8c66b;
    position: relative;
    border-radius: 15px;
    margin-bottom: 8px;
}
.form_progress_add_element h3{
    font-size: 24px;
    word-wrap: break-word;
}
.form_progress_add_element p{
    font-size: 20px;
    word-wrap: break-word;
}
.form_progress_add_element_delete{
    position: absolute;
    right: 5px;
    top: 5px;
    border: 1px solid #E8222D;
    color: #E8222D!important;
    font-size: 20px!important;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s ease;
}
.form_progress_add_element_delete:hover{
    background-color: #f8c66b;
}
.form_progress>span{
    font-size: 48px;
}
.form_ingridients>span{
    font-size: 48px;
}
.formAdd_box_con {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

@media screen and (max-width: 450px){
    .zagMain h1{
        font-size: 50px;
    }
    .formAdd_box_con input{
        width: 300px;
    }
}
.zagMain>span{
    text-decoration: underline;
    cursor: pointer;
    transition: all 0.3s ease;
}
.zagMain>span:hover{
    color: #f6c04d;
}
.zagMain>span::selection {
    background: transparent;
}

.error{
    color: #E8222D;
    text-align: center;
    font-size: 28px;
    cursor: pointer;
    position: relative;
}
.error:hover .error_span_1{
    opacity: 0;
}
.error:hover .error_span_2{
    opacity: 1;
}

.error_span_1{
    transition: all 0.3s ease;
}
.error_span_2{
    opacity: 0;
    position: absolute;
    left: 0;
    width: 100%;
    text-align: center;
    transition: all 0.3s ease;
}
.error::selection {
    background: transparent;
}
.back{
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: 30px;
    color: #E8222D;
    text-decoration: none;
    cursor: pointer;
    transition: all 0.3s ease;
}
.back:hover{
    color: #e3000b;

}




