.container{
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 30px;
}
.main{

}
.images{

}
.sdvoen{
    display: flex;
    width: 100%;
    flex-direction: row;
    margin-top: 20px;
    flex-wrap: wrap;
}
.disert{
    width: 50%;
    height: 500px;
    background-image: url("../../files/2ae6e2267fe3a2a7f242d9cebf62212b.jpg");
    background-size: cover;
    background-position: center;
    filter: brightness(20%);
    color: #F5E0A3;
    position: relative;
    transition: all 0.5s ease;
}
.osniva{
    width: 50%;
    height: 500px;
    background-image: url("../../files/yandex_eda-1288x724.jpg");
    background-size: cover;
    background-position: center;
    filter: brightness(20%);
    color: #F5E0A3;
    position: relative;
    transition: all 0.5s ease;
}
.disert:hover{
    filter: brightness(90%);
}
.osniva:hover{
    filter: brightness(90%);
}
.disert_text{
    position: absolute;
    bottom: 5px;
    text-align: center;
    font-size: 48px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.66);
}
.osnova_text{
    position: absolute;
    bottom: 5px;
    text-align: center;
    font-size: 48px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.66);

}
.zagolovok h1{
    font-size: 50px;
    color: #F5E0A3;
    text-align: center;
}
@media screen and (max-width: 650px){
    .sdvoen{
        flex-direction: column;
        margin-bottom: 30px;
    }
    .disert{
        width: 100%;
    }
    .osniva {
        width: 100%;
    }
}











